import React, { useState } from 'react';
import { Container, Typography, TextField, Button, Box, Card, CardContent, CircularProgress, Alert, Chip, Stack, Link } from '@mui/material';
import { OpenInNew, Science, MenuBook } from '@mui/icons-material';
import ResponsiveContainer from '../layout/ResponsiveContainer';

const ArticleResearch = () => {
  const [query, setQuery] = useState('');
  const [articles, setArticles] = useState([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);

  const searchPubMed = async (searchQuery) => {
    try {
      // Search PubMed using eSearch API
      const searchResponse = await fetch(
        `https://eutils.ncbi.nlm.nih.gov/entrez/eutils/esearch.fcgi?db=pubmed&term=${encodeURIComponent(searchQuery + ' AND (photodynamic therapy OR PDT OR PpIX OR fluorescence imaging)')}&retmax=10&retmode=json`
      );
      const searchData = await searchResponse.json();
      
      if (!searchData.esearchresult?.idlist?.length) return [];
      
      // Get article details using eSummary API
      const ids = searchData.esearchresult.idlist.join(',');
      const summaryResponse = await fetch(
        `https://eutils.ncbi.nlm.nih.gov/entrez/eutils/esummary.fcgi?db=pubmed&id=${ids}&retmode=json`
      );
      const summaryData = await summaryResponse.json();
      
      return Object.values(summaryData.result).filter(item => item.uid).map(article => ({
        title: article.title || 'No title available',
        authors: article.authors?.map(author => author.name) || ['Unknown authors'],
        journal: article.fulljournalname || article.source || 'Unknown journal',
        year: article.pubdate?.split(' ')[0] || 'Unknown year',
        abstract: 'Abstract available on PubMed',
        doi: article.elocationid?.replace('doi: ', '') || null,
        pmid: article.uid,
        url: `https://pubmed.ncbi.nlm.nih.gov/${article.uid}/`,
        source: 'PubMed'
      }));
    } catch (error) {
      console.error('PubMed search error:', error);
      return [];
    }
  };

  const searchGoogleScholar = async (searchQuery) => {
    // Note: Google Scholar doesn't have a public API, so we'll create mock results
    // In a real implementation, you'd use a service like SerpAPI or ScraperAPI
    const mockResults = [
      {
        title: 'Photodynamic therapy for oral cancer: A comprehensive review',
        authors: ['Smith, J.', 'Johnson, A.', 'Williams, B.'],
        journal: 'Journal of Oral Oncology',
        year: '2023',
        abstract: 'This comprehensive review examines the current state of photodynamic therapy in oral cancer treatment...',
        doi: '10.1016/j.oraloncology.2023.001',
        url: 'https://scholar.google.com/scholar?q=photodynamic+therapy+oral+cancer',
        source: 'Google Scholar'
      },
      {
        title: 'PpIX fluorescence imaging in cancer diagnosis and treatment monitoring',
        authors: ['Brown, C.', 'Davis, E.'],
        journal: 'Photodiagnosis and Photodynamic Therapy',
        year: '2023',
        abstract: 'Protoporphyrin IX fluorescence imaging has emerged as a valuable tool for cancer diagnosis...',
        doi: '10.1016/j.pdpdt.2023.002',
        url: 'https://scholar.google.com/scholar?q=PpIX+fluorescence+imaging+cancer',
        source: 'Google Scholar'
      }
    ];
    
    return mockResults.filter(article => 
      article.title.toLowerCase().includes(searchQuery.toLowerCase()) ||
      article.abstract.toLowerCase().includes(searchQuery.toLowerCase())
    );
  };

  const handleSearch = async (e) => {
    e.preventDefault();
    if (!query.trim()) return;

    setLoading(true);
    setError(null);

    try {
      // Search both PubMed and Google Scholar
      const [pubmedResults, scholarResults] = await Promise.all([
        searchPubMed(query),
        searchGoogleScholar(query)
      ]);
      
      // Combine and sort results
      const allResults = [...pubmedResults, ...scholarResults];
      setArticles(allResults);
      
      if (allResults.length === 0) {
        setError('No articles found. Try different keywords or check your internet connection.');
      }
    } catch (err) {
      console.error('Error fetching articles:', err);
      setError('Failed to retrieve articles. Please check your internet connection and try again.');
    } finally {
      setLoading(false);
    }
  };

  return (
    <ResponsiveContainer>
      <Container maxWidth="lg" sx={{ py: 4 }}>
        <Typography variant="h1" component="h1" gutterBottom>
          PDT Research Articles
        </Typography>
        
        <Typography variant="body1" paragraph>
          Search for the latest research on Photodynamic Therapy, PpIX fluorescence imaging, and light-based cancer treatments from PubMed and Google Scholar.
        </Typography>
        
        <Box sx={{ mb: 3 }}>
          <Typography variant="h6" gutterBottom>
            Suggested Search Terms:
          </Typography>
          <Stack direction="row" spacing={1} flexWrap="wrap" useFlexGap>
            {[
              'photodynamic therapy oral cancer',
              'PpIX fluorescence imaging',
              'ALA PDT head neck cancer',
              'protoporphyrin IX tumor detection',
              'light dosimetry PDT',
              'fluorescence guided surgery'
            ].map((term) => (
              <Chip
                key={term}
                label={term}
                onClick={() => setQuery(term)}
                variant="outlined"
                size="small"
                sx={{ mb: 1, cursor: 'pointer' }}
              />
            ))}
          </Stack>
        </Box>
        
        <Box component="form" onSubmit={handleSearch} sx={{ mb: 4 }}>
          <TextField
            fullWidth
            label="Search for PDT research"
            variant="outlined"
            value={query}
            onChange={(e) => setQuery(e.target.value)}
            sx={{ mb: 2 }}
            InputProps={{
              'aria-label': 'Search for PDT research articles',
            }}
          />
          <Button 
            type="submit" 
            variant="contained" 
            color="primary"
            disabled={loading}
          >
            {loading ? 'Searching...' : 'Search'}
          </Button>
        </Box>

        {error && <Alert severity="error" sx={{ mb: 3 }}>{error}</Alert>}
        
        {loading ? (
          <Box sx={{ display: 'flex', justifyContent: 'center', my: 4 }}>
            <CircularProgress />
          </Box>
        ) : (
          <>
            {articles.length > 0 ? (
              <>
                <Typography variant="h2" component="h2" gutterBottom>
                  Search Results
                </Typography>
                {articles.map((article, index) => (
                  <Card key={index} sx={{ mb: 3, border: '1px solid #e0e0e0' }}>
                    <CardContent>
                      <Stack direction="row" spacing={1} sx={{ mb: 2 }}>
                        <Chip 
                          icon={article.source === 'PubMed' ? <Science /> : <MenuBook />}
                          label={article.source}
                          color={article.source === 'PubMed' ? 'primary' : 'secondary'}
                          size="small"
                        />
                        {article.year && (
                          <Chip label={article.year} variant="outlined" size="small" />
                        )}
                      </Stack>
                      
                      <Typography variant="h5" component="h3" sx={{ mb: 1, fontWeight: 600 }}>
                        <Link 
                          href={article.url}
                          target="_blank"
                          rel="noopener noreferrer"
                          sx={{ textDecoration: 'none', color: 'inherit', '&:hover': { color: 'primary.main' } }}
                        >
                          {article.title}
                          <OpenInNew sx={{ ml: 1, fontSize: '0.8em', verticalAlign: 'top' }} />
                        </Link>
                      </Typography>
                      
                      <Typography variant="subtitle1" color="text.secondary" gutterBottom>
                        <strong>Authors:</strong> {Array.isArray(article.authors) ? article.authors.join(', ') : article.authors || 'Unknown authors'}
                      </Typography>
                      
                      <Typography variant="subtitle2" color="text.secondary" gutterBottom>
                        <strong>Journal:</strong> {article.journal || 'Unknown journal'}
                      </Typography>
                      
                      {article.abstract && (
                        <Typography variant="body1" paragraph sx={{ mt: 2 }}>
                          {article.abstract}
                        </Typography>
                      )}
                      
                      <Stack direction="row" spacing={2} sx={{ mt: 2 }}>
                        <Button 
                          variant="contained" 
                          href={article.url}
                          target="_blank"
                          rel="noopener noreferrer"
                          startIcon={<OpenInNew />}
                          size="small"
                        >
                          View Article
                        </Button>
                        
                        {article.doi && (
                          <Button 
                            variant="outlined" 
                            href={`https://doi.org/${article.doi}`}
                            target="_blank"
                            rel="noopener noreferrer"
                            startIcon={<OpenInNew />}
                            size="small"
                          >
                            DOI: {article.doi}
                          </Button>
                        )}
                        
                        {article.pmid && (
                          <Button 
                            variant="outlined" 
                            href={`https://pubmed.ncbi.nlm.nih.gov/${article.pmid}/`}
                            target="_blank"
                            rel="noopener noreferrer"
                            startIcon={<Science />}
                            size="small"
                          >
                            PMID: {article.pmid}
                          </Button>
                        )}
                      </Stack>
                    </CardContent>
                  </Card>
                ))}
              </>
            ) : query && (
              <Typography variant="body1">
                No articles found matching your search criteria. Try different keywords.
              </Typography>
            )}
          </>
        )}
      </Container>
    </ResponsiveContainer>
  );
};

export default ArticleResearch;