import React, { useState } from 'react';
import { Container, Typography, Box, Button, Card, CardContent, Grid, CircularProgress, Alert, Paper } from '@mui/material';
import ResponsiveContainer from '../layout/ResponsiveContainer';
import ResponsiveImage from '../common/ResponsiveImage';

const ImageAnalysis = () => {
  const [selectedFile, setSelectedFile] = useState(null);
  const [previewUrl, setPreviewUrl] = useState(null);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);
  const [analysisResult, setAnalysisResult] = useState(null);

  const handleFileChange = (event) => {
    const file = event.target.files[0];
    if (!file) return;

    // Check if file is an image
    if (!file.type.match('image.*')) {
      setError('Please select an image file (JPEG, PNG, etc.)');
      return;
    }

    setSelectedFile(file);
    setError(null);
    
    // Create preview URL
    const reader = new FileReader();
    reader.onloadend = () => {
      setPreviewUrl(reader.result);
    };
    reader.readAsDataURL(file);
  };

  const handleUpload = async () => {
    if (!selectedFile) {
      setError('Please select an image to analyze');
      return;
    }

    setLoading(true);
    setError(null);

    const formData = new FormData();
    formData.append('image', selectedFile);

    try {
      const response = await fetch('/api/analysis/fluorescence', {
        method: 'POST',
        body: formData,
      });

      if (!response.ok) throw new Error('Failed to analyze image');
      
      const data = await response.json();
      setAnalysisResult(data);
    } catch (err) {
      console.error('Error analyzing image:', err);
      setError('Failed to analyze image. Please try again later.');
    } finally {
      setLoading(false);
    }
  };

  const handleReset = () => {
    setSelectedFile(null);
    setPreviewUrl(null);
    setAnalysisResult(null);
    setError(null);
  };

  return (
    <ResponsiveContainer>
      <Container maxWidth="lg" sx={{ py: 4 }}>
        <Typography variant="h1" component="h1" gutterBottom>
          PpIX Fluorescence Image Analysis
        </Typography>
        
        <Typography variant="body1" paragraph>
          Upload and analyze PpIX fluorescence images to quantify photosensitizer uptake and distribution in oral cancer tissues.
        </Typography>
        
        <Grid container spacing={4}>
          <Grid item xs={12} md={6}>
            <Card variant="outlined" sx={{ height: '100%' }}>
              <CardContent>
                <Typography variant="h2" component="h2" gutterBottom>
                  Upload Image
                </Typography>
                
                <Box sx={{ my: 3, textAlign: 'center' }}>
                  <input
                    accept="image/*"
                    style={{ display: 'none' }}
                    id="image-upload"
                    type="file"
                    onChange={handleFileChange}
                    aria-label="Upload fluorescence image"
                  />
                  <label htmlFor="image-upload">
                    <Button variant="contained" component="span">
                      Select Image
                    </Button>
                  </label>
                </Box>
                
                {previewUrl && (
                  <Box sx={{ my: 3, textAlign: 'center' }}>
                    <Paper elevation={3} sx={{ p: 2, maxWidth: '100%', overflow: 'hidden' }}>
                      <ResponsiveImage 
                        src={previewUrl} 
                        alt="Preview of selected fluorescence image" 
                        style={{ maxHeight: '300px', maxWidth: '100%' }}
                      />
                    </Paper>
                    <Typography variant="body2" sx={{ mt: 1 }}>
                      {selectedFile?.name}
                    </Typography>
                  </Box>
                )}
                
                {error && <Alert severity="error" sx={{ my: 2 }}>{error}</Alert>}
                
                <Box sx={{ mt: 3, display: 'flex', justifyContent: 'center', gap: 2 }}>
                  <Button 
                    variant="contained" 
                    color="primary" 
                    onClick={handleUpload}
                    disabled={!selectedFile || loading}
                  >
                    {loading ? <CircularProgress size={24} /> : 'Analyze Image'}
                  </Button>
                  
                  <Button 
                    variant="outlined" 
                    onClick={handleReset}
                    disabled={loading}
                  >
                    Reset
                  </Button>
                </Box>
              </CardContent>
            </Card>
          </Grid>
          
          <Grid item xs={12} md={6}>
            <Card variant="outlined" sx={{ height: '100%' }}>
              <CardContent>
                <Typography variant="h2" component="h2" gutterBottom>
                  Analysis Results
                </Typography>
                
                {loading ? (
                  <Box sx={{ display: 'flex', justifyContent: 'center', my: 4 }}>
                    <CircularProgress />
                  </Box>
                ) : analysisResult ? (
                  <Box>
                    <Typography variant="h3" component="h3" gutterBottom sx={{ mt: 2 }}>
                      PpIX Fluorescence Metrics
                    </Typography>
                    
                    <Grid container spacing={2} sx={{ mb: 3 }}>
                      <Grid item xs={6}>
                        <Paper elevation={2} sx={{ p: 2, textAlign: 'center' }}>
                          <Typography variant="h4" component="h4">
                            {analysisResult.meanIntensity}
                          </Typography>
                          <Typography variant="body2" color="text.secondary">
                            Mean Intensity
                          </Typography>
                        </Paper>
                      </Grid>
                      <Grid item xs={6}>
                        <Paper elevation={2} sx={{ p: 2, textAlign: 'center' }}>
                          <Typography variant="h4" component="h4">
                            {analysisResult.maxIntensity}
                          </Typography>
                          <Typography variant="body2" color="text.secondary">
                            Max Intensity
                          </Typography>
                        </Paper>
                      </Grid>
                      <Grid item xs={6}>
                        <Paper elevation={2} sx={{ p: 2, textAlign: 'center' }}>
                          <Typography variant="h4" component="h4">
                            {analysisResult.tumorToNormalRatio}
                          </Typography>
                          <Typography variant="body2" color="text.secondary">
                            Tumor-to-Normal Ratio
                          </Typography>
                        </Paper>
                      </Grid>
                      <Grid item xs={6}>
                        <Paper elevation={2} sx={{ p: 2, textAlign: 'center' }}>
                          <Typography variant="h4" component="h4">
                            {analysisResult.heterogeneityIndex}
                          </Typography>
                          <Typography variant="body2" color="text.secondary">
                            Heterogeneity Index
                          </Typography>
                        </Paper>
                      </Grid>
                    </Grid>
                    
                    {analysisResult.processedImageUrl && (
                      <Box sx={{ mt: 3, textAlign: 'center' }}>
                        <Typography variant="h3" component="h3" gutterBottom>
                          Processed Image
                        </Typography>
                        <Paper elevation={3} sx={{ p: 2, maxWidth: '100%', overflow: 'hidden' }}>
                          <ResponsiveImage 
                            src={analysisResult.processedImageUrl} 
                            alt="Processed fluorescence image with intensity mapping" 
                            style={{ maxHeight: '300px', maxWidth: '100%' }}
                          />
                        </Paper>
                      </Box>
                    )}
                    
                    <Typography variant="h3" component="h3" gutterBottom sx={{ mt: 3 }}>
                      Clinical Interpretation
                    </Typography>
                    <Typography variant="body1" paragraph>
                      {analysisResult.clinicalInterpretation || 'No clinical interpretation available.'}
                    </Typography>
                    
                    <Button 
                      variant="outlined" 
                      fullWidth
                      onClick={() => window.print()}
                      sx={{ mt: 2 }}
                    >
                      Export Results
                    </Button>
                  </Box>
                ) : (
                  <Box sx={{ display: 'flex', justifyContent: 'center', alignItems: 'center', height: '300px' }}>
                    <Typography variant="body1" color="text.secondary">
                      Upload and analyze an image to see results
                    </Typography>
                  </Box>
                )}
              </CardContent>
            </Card>
          </Grid>
        </Grid>
      </Container>
    </ResponsiveContainer>
  );
};

export default ImageAnalysis;