import React, { useState } from 'react';
import { Container, Typography, Box, TextField, Button, Card, CardContent, Divider, Stepper, Step, StepLabel, CircularProgress, Alert } from '@mui/material';
import ResponsiveContainer from '../layout/ResponsiveContainer';

const TreatmentPlan = () => {
  const [patientData, setPatientData] = useState({
    age: '',
    gender: '',
    cancerType: '',
    cancerStage: '',
    tumorLocation: '',
    previousTreatments: ''
  });
  const [activeStep, setActiveStep] = useState(0);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);
  const [treatmentPlan, setTreatmentPlan] = useState(null);

  const steps = ['Patient Information', 'Treatment Parameters', 'Generated Plan'];

  const handleChange = (e) => {
    const { name, value } = e.target;
    setPatientData(prev => ({
      ...prev,
      [name]: value
    }));
  };

  const handleNext = () => {
    setActiveStep((prevStep) => prevStep + 1);
  };

  const handleBack = () => {
    setActiveStep((prevStep) => prevStep - 1);
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setLoading(true);
    setError(null);

    try {
      const response = await fetch('/api/treatment/plan', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(patientData),
      });

      if (!response.ok) throw new Error('Failed to generate treatment plan');
      
      const data = await response.json();
      setTreatmentPlan(data);
      handleNext();
    } catch (err) {
      console.error('Error generating treatment plan:', err);
      setError('Failed to generate treatment plan. Please try again later.');
    } finally {
      setLoading(false);
    }
  };

  const renderStepContent = (step) => {
    switch (step) {
      case 0:
        return (
          <Box component="form" sx={{ mt: 3 }}>
            <Typography variant="h3" component="h3" gutterBottom>
              Patient Information
            </Typography>
            <TextField
              fullWidth
              label="Age"
              name="age"
              type="number"
              value={patientData.age}
              onChange={handleChange}
              margin="normal"
              required
            />
            <TextField
              fullWidth
              label="Gender"
              name="gender"
              select
              SelectProps={{ native: true }}
              value={patientData.gender}
              onChange={handleChange}
              margin="normal"
              required
            >
              <option value=""></option>
              <option value="male">Male</option>
              <option value="female">Female</option>
              <option value="other">Other</option>
            </TextField>
            <TextField
              fullWidth
              label="Cancer Type"
              name="cancerType"
              value={patientData.cancerType}
              onChange={handleChange}
              margin="normal"
              required
            />
            <TextField
              fullWidth
              label="Cancer Stage"
              name="cancerStage"
              value={patientData.cancerStage}
              onChange={handleChange}
              margin="normal"
              required
            />
            <Box sx={{ display: 'flex', justifyContent: 'flex-end', mt: 3 }}>
              <Button
                variant="contained"
                onClick={handleNext}
                sx={{ mt: 3, ml: 1 }}
              >
                Next
              </Button>
            </Box>
          </Box>
        );
      case 1:
        return (
          <Box component="form" onSubmit={handleSubmit} sx={{ mt: 3 }}>
            <Typography variant="h3" component="h3" gutterBottom>
              Treatment Parameters
            </Typography>
            <TextField
              fullWidth
              label="Tumor Location"
              name="tumorLocation"
              value={patientData.tumorLocation}
              onChange={handleChange}
              margin="normal"
              required
            />
            <TextField
              fullWidth
              label="Previous Treatments"
              name="previousTreatments"
              value={patientData.previousTreatments}
              onChange={handleChange}
              margin="normal"
              multiline
              rows={4}
            />
            {error && <Alert severity="error" sx={{ mt: 2 }}>{error}</Alert>}
            <Box sx={{ display: 'flex', justifyContent: 'flex-end', mt: 3 }}>
              <Button onClick={handleBack} sx={{ mt: 3, mr: 1 }}>
                Back
              </Button>
              <Button
                variant="contained"
                type="submit"
                disabled={loading}
                sx={{ mt: 3, ml: 1 }}
              >
                {loading ? <CircularProgress size={24} /> : 'Generate Plan'}
              </Button>
            </Box>
          </Box>
        );
      case 2:
        return (
          <Box sx={{ mt: 3 }}>
            <Typography variant="h3" component="h3" gutterBottom>
              PDT Treatment Plan
            </Typography>
            {treatmentPlan ? (
              <Card variant="outlined" sx={{ mb: 3 }}>
                <CardContent>
                  <Typography variant="h4" component="h4" gutterBottom>
                    Patient Information
                  </Typography>
                  <Typography variant="body1">
                    <strong>Age:</strong> {patientData.age}
                  </Typography>
                  <Typography variant="body1">
                    <strong>Gender:</strong> {patientData.gender}
                  </Typography>
                  <Typography variant="body1">
                    <strong>Cancer Type:</strong> {patientData.cancerType}
                  </Typography>
                  <Typography variant="body1">
                    <strong>Cancer Stage:</strong> {patientData.cancerStage}
                  </Typography>
                  <Typography variant="body1">
                    <strong>Tumor Location:</strong> {patientData.tumorLocation}
                  </Typography>
                  
                  <Divider sx={{ my: 2 }} />
                  
                  <Typography variant="h4" component="h4" gutterBottom>
                    Recommended PDT Protocol
                  </Typography>
                  <Typography variant="body1">
                    <strong>Photosensitizer:</strong> {treatmentPlan.photosensitizer}
                  </Typography>
                  <Typography variant="body1">
                    <strong>Administration Route:</strong> {treatmentPlan.administrationRoute}
                  </Typography>
                  <Typography variant="body1">
                    <strong>Drug-Light Interval:</strong> {treatmentPlan.drugLightInterval}
                  </Typography>
                  <Typography variant="body1">
                    <strong>Light Source:</strong> {treatmentPlan.lightSource}
                  </Typography>
                  <Typography variant="body1">
                    <strong>Wavelength:</strong> {treatmentPlan.wavelength}
                  </Typography>
                  <Typography variant="body1">
                    <strong>Fluence Rate:</strong> {treatmentPlan.fluenceRate}
                  </Typography>
                  <Typography variant="body1">
                    <strong>Total Light Dose:</strong> {treatmentPlan.totalLightDose}
                  </Typography>
                  <Typography variant="body1">
                    <strong>Treatment Duration:</strong> {treatmentPlan.treatmentDuration}
                  </Typography>
                  
                  <Divider sx={{ my: 2 }} />
                  
                  <Typography variant="h4" component="h4" gutterBottom>
                    Clinical Notes
                  </Typography>
                  <Typography variant="body1" paragraph>
                    {treatmentPlan.clinicalNotes}
                  </Typography>
                  
                  <Typography variant="h4" component="h4" gutterBottom>
                    Follow-up Recommendations
                  </Typography>
                  <Typography variant="body1" paragraph>
                    {treatmentPlan.followUpRecommendations}
                  </Typography>
                </CardContent>
              </Card>
            ) : (
              <Typography variant="body1">
                No treatment plan generated. Please go back and submit the form.
              </Typography>
            )}
            <Box sx={{ display: 'flex', justifyContent: 'flex-end', mt: 3 }}>
              <Button onClick={handleBack} sx={{ mt: 3, mr: 1 }}>
                Back
              </Button>
              <Button
                variant="contained"
                onClick={() => window.print()}
                sx={{ mt: 3, ml: 1 }}
              >
                Print Plan
              </Button>
            </Box>
          </Box>
        );
      default:
        return null;
    }
  };

  return (
    <ResponsiveContainer>
      <Container maxWidth="lg" sx={{ py: 4 }}>
        <Typography variant="h1" component="h1" gutterBottom>
          PDT Treatment Planning
        </Typography>
        
        <Typography variant="body1" paragraph>
          Generate evidence-based Photodynamic Therapy treatment plans for oral cancer patients.
        </Typography>
        
        <Stepper activeStep={activeStep} sx={{ pt: 3, pb: 5 }}>
          {steps.map((label) => (
            <Step key={label}>
              <StepLabel>{label}</StepLabel>
            </Step>
          ))}
        </Stepper>
        
        {renderStepContent(activeStep)}
      </Container>
    </ResponsiveContainer>
  );
};

export default TreatmentPlan;